@import url(https://fonts.googleapis.com/css2?family=Fraunces:wght@900&display=swap);
body {
  margin: 0;
  font-family: 'Barlow', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  font-size: 18px;
}
 
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.img-fluid{
  width: 100% !important;
}
.App{

}
*{
    transition: all 0.5s ease-in-out;
}

@media (max-width:991px)
{
    /* .navbar-toggler{
        position: relative;
    } */
    .navbar-collapse{
        top: 30px;
        background-color: #fff;
        position: relative;
        z-index: 333;  
        padding-top: 10px;
        padding-bottom: 5px;
    }
    .navbar-collapse::after{
        content: '';
        border-width: 30px;
        border-style: solid;
        border-color: transparent  #fff transparent  transparent;
        position: absolute;
        top: -30px;
        right: 0;
        z-index: 22;
    }
.navbar-dark .navbar-nav .nav-link{color:hsl(167, 40%, 24%) !important; margin-bottom: 10px;}

  nav  .nav-item.active .nav-link,
 nav   .nav-item .nav-link:hover{
        background-color: hsl(51, 100%, 49%) !important;
    }
    
}
.navbar-brand{
    font-size: 30px !important;
}
.navbar-brand:hover{
    text-shadow: 2px 2px 5px #eee ;
}
.navbar-dark .navbar-nav .nav-link{ display:inline-block;color: #fff;}
.navbar-dark .navbar-nav .nav-link{
    margin-right: 10px;
}
nav .nav-item.active .nav-link,
nav .nav-item .nav-link:hover{
    background-color:#fff;
    color: hsl(198, 62%, 26%) !important;
    font-weight: 600;
    padding: 5px 15px;
    border-radius: 20px;
}
.nav-link{
    cursor: pointer;
}


.about{
   
    text-align: center;
    min-height: 100vh;
    background-image: url(/sunnyside/static/media/image-header.49b52ffa.jpg);
    background-size: cover;
    background-position: center;

}
.about h1{
    margin-top: 50px;
    margin-bottom: 30px;
    letter-spacing: 8px;
    font-size: 50px;
    font-family: 'Fraunces', serif;
    color:#fff
}

.service-desc{
    padding: 10% ;
    
}

@media(max-width:767px)
{
    .service-desc{
        text-align: center;
    }
}
.service-desc h2, .services h3{
    font-family: 'Fraunces', serif;
    letter-spacing: 2px;
    margin-bottom: 20px;
}
.service-desc p {
    line-height: 1.8;
    margin-bottom: 30px;
    color:  hsl(210, 4%, 67%);
}
.services .photography-text { color: hsl(198, 62%, 26%) !important;}
.services .graphic-design-text{
    color: hsl(198, 62%, 26%) !important;
}

.col-md-6.img{
    padding: 0;
}

.my-pos-relative{
    overflow: hidden;
    position: relative;
}
.my-pos-abs{
    position: absolute;
    bottom: 10%;
    left: 10%;
    right: 10%;
}
.testimonials {
    padding:50px ;
}

.testimonials h2 { 
    letter-spacing: 3px;
    margin-bottom: 50px;
}

.testimonials h2,
.testimonials h4{
    font-family: 'Fraunces', serif;
}
.testimonials img{

    border-radius: 50%;
    margin-bottom: 60px;
    width: 100px;
}

.testimonials p{
  color:hsl(213, 9%, 39%);
  margin-bottom: 60px;
}
.testimonials h4 {
    margin-bottom: 30px;
}
.testimonials .jop,.testimonials h2 {
    color: hsl(210, 4%, 67%);
}

@media (max-width:767px) {
    .testimonials .col-md-4:not(:nth-child(3)) {
        margin-bottom: 40px;
        border-bottom: .5px solid hsl(210, 4%, 67%);
    }
}


.gallery .col-md-3 {
    padding:0;
}
@media (max-width:545px)
{
    .my-col-xs-6{
        width: 50%;
    }
}
footer{
    padding-top: 50px;
    padding-bottom: 50px;
    background-color:  hsl(168, 53%, 60%);;
}
footer,footer .nav-link{
    color: hsl(167, 40%, 24%) !important;
}
footer h3{
    font-family: 'Fraunces', serif;
}
footer .navbar-nav{
    flex-direction: row;
    justify-content: center;
}
footer li{
    display: inline-block !important;
    margin: 20px;
}
footer img{
    margin: 10px;
}
.scroll-top{
    height: 50px;
    width: 70px;
    position: fixed;
    right: 20px;
    bottom: 20px;
    padding: 5px 10px;
    border-radius: 10px;
    box-shadow: 5px 5px 5px rgba(238, 238, 238, 0.678);
    background-color: hsl(167, 40%, 24%) !important;
    color: #fff;
    text-align: center;
    cursor: pointer;
}
